import { Component, Vue, Emit, Prop, Watch } from 'vue-property-decorator';

import 'video.js/dist/video-js.css';

import _videojs from 'video.js';
const videojs = (window as any).videojs || _videojs;
 // video.js 6.x flashvideo.jsrtmp
import 'videojs-flash';

@Component({
  name: 'videojs-player',
})
export default class VideojsPlayer extends Vue {
  /* ------------------------ INPUT & OUTPUT ------------------------ */
  @Prop({type: String, default: ''}) public source!: string;
  @Prop({type: Object, default() { return {}; }}) private options!: object;

  /* ------------------------ VUEX (vuex getter & vuex action) ------------------------ */

  /* ------------------------ LIFECYCLE HOOKS (created & mounted & ...) ------------------------ */
  private mounted() {
    this.initVideojsPlayer();
  }
  // public playerCurrentTime: any = 0;
  /* ------------------------ COMPONENT STATE (data & computed & model) ------------------------ */
  public player: any = null;
  public isPlay: boolean = false;
  private loading: boolean = false;

  public getVideojsTimeState() {
    return {currentTime: this.player.cache_.currentTime, duration: this.player.cache_.duration};
  }
  /* ------------------------ WATCH ------------------------ */

  /* ------------------------ METHODS ------------------------ */
  private initVideojsPlayer() {
    this.player = videojs(this.$refs.videoPlayer, this.options, () => {
      this.player.volume(0.2);

      this.player.on('ended', () => {
        this.isPlay = false;
        console.log('');
      });
      this.player.on('error', () => {
        console.log('');
      });
      // this.isPlay = false;
      // this.player.on('progress', () => {
        // this.isPlay = true;
        // this.player.play();
        // setTimeout(() => {
        //   console.log('---pasued ');
        //   this.player.pause()
        // }, 5000);
      // });
      // this.player.on('userActions', (e: any) => {
      //   console.log('userActions', e)
      //   if (e.which === 88){
      //     this.isPlay = false;
      //     this.player.pause();
      //   }
      //   if (e.which === 89){
      //     this.isPlay = true;
      //     this.player.play();
      //   }
      // });

      // this.player.on('timeupdate', () => { // currenttime
      //   console.log('timeupdate')
      // });
        // this.isPlay = true;
        // if (this.player.cache_.currentTime != '0:00' && this.isPlay) { //  
        // this.isPlay = false;
        // }
      // this.player.on('click', (e: any) => {
      //   console.log('click8', e.which)
      // })

      this.player.on('play', () => {
        console.log('playing');
        this.isPlay = true;
      });
      this.player.on('pause', () => {
        console.log('paused ');
        this.isPlay = false;
      });

    });
    // dynamicclassName, 
    this.player.addClass('my_videojs_player');
  }
  private  beforeDestroy() {
    if (this.player) {
      this.player.dispose();
    }
  }
  public play() {
    this.isPlay = true;
    this.player.play();
  }
  public pause() {
    this.isPlay = false;
    this.player.pause();
  }
  /**
   * src
   */
  public refreshPlay() {
    this.player.src(this.source);
    this.player.load(this.source);
    this.player.play();
  }
}

